import React, {Component} from "react";
import {Link} from "react-router-dom";

class Contact extends Component{
    constructor(props) {
        super(props);

    }

    render() {
        return (
            <div>

                <nav className="navbar navbar-expand-xl navbar-light bg-white animated slideInDown">
                    <a className="navbar-brand" href="#">Global Digital Technology </a><img src="bootstrap/images/logo.png"/>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText"
                            aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"/>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarText">
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item ">
                                <Link to="/" className="nav-link" >Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/Features" className="nav-link">Features</Link>
                            </li>
                            <li className="nav-item ">
                                <Link to="/Services" className="nav-link">Services</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/Pricing" className="nav-link">Pricing</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/Projects" className="nav-link">Projects</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/Reviews" className="nav-link" >Reviews</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/Contact" className="nav-link" style={{backgroundColor: "#00b0ff"}}>Contact</Link>
                            </li>
                        </ul>
                        <span className="navbar-text text-dark"> +91 9876543210, New York</span>
                    </div>
                </nav>

                <section id="sliderSection">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-6 col-sm-12">
                                <img src="bootstrap/images/contact.png" className="img-fluid "/>
                            </div>
                            <div className="col-md-6 col-sm-12">
                                <h1>Contact and Get in Touch</h1>
                                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consectetur dignissimos
                                    dolorum exercitationem in ipsa laudantium maxime mollitia quaerat vero,
                                    voluptatum.</p>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="form-section">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-6">
                                <h3>Send Your Details</h3>
                                <form>
                                    <div className="form-group">
                                        <label>Name</label>
                                        <input type="text" placeholder="Your Name" className="form-control"/>
                                    </div>
                                    <div className="form-group">
                                        <label>Email</label>
                                        <input type="text" placeholder="Your Email" className="form-control"/>
                                    </div>
                                    <div className="form-group">
                                        <label>Mobile</label>
                                        <input type="text" placeholder="Your Mobile" className="form-control"/>
                                    </div>
                                    <div className="form-group">
                                        <label>Package</label>
                                        <input type="text" placeholder="Your Package" className="form-control"/>
                                    </div>
                                    <a type="button" className="" value="Submit">Submit</a>
                                </form>
                            </div>
                            <div className="col-md-6">
                                <div className="map">
                                    <iframe
                                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6009069.56808649!2d-80.25903546643882!3d42.658897159555785!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4ccc4bf0f123a5a9%3A0xddcfc6c1de189567!2sNew%20York%2C%20USA!5e0!3m2!1sen!2sin!4v1581428423441!5m2!1sen!2sin"
                                        width="600" height="450" frameBorder="0" style={{border:0}}
                                        allowFullScreen=""/>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="mt-3">
                    <div className="container">
                        <div className="col-md-10">
                            <div className="jumbotron">
                                <h3 className="">Thank You for Visiting.......!</h3>
                                <p className="">This is a simple hero unit, a simple jumbotron-style component for
                                    calling extra attention to featured content or information.</p>
                                <hr className=""/>
                                    <p>It uses utility classes for typography and spacing to space content out within
                                        the larger container.</p>
                            </div>
                        </div>
                    </div>
                </section>


                <footer>
                    <div className="container">
                        <div className="row">
                            <div className="col-md-4">
                                <p>Walmart Street, Lorem ipsum dolor sit amet, consectetur adipisicing elit. Minus,
                                    voluptates?</p>
                                <img src="bootstrap/images/logo.png" className="img-fluid"/>
                            </div>
                            <div className="col-md-4">
                                <h3>Address : </h3>
                                <span>New York, USA</span>
                                <p>Walmart Street, Lorem ipsum dolor sit amet, consectetur adipisicing elit. Minus,
                                    voluptates?</p>
                            </div>
                            <div className="col-md-4">
                                <div>
                                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Assumenda at dolores
                                        esse iure iusto magni minus, omnis porro quam velit. Assumenda consequuntur
                                        culpa dicta, doloribus eligendi esse, expedita facere magni molestias officia,
                                        omnis quas quidem quis saepe tempora unde vitae?</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>


            </div>
        );
    }

}

export default Contact;